import React from 'react';
import './list.css';
import { start } from '../../assets/serbu';

const List = (props) => {
  const { data } = props;

  return (
    <div className='wrapper-box'>
      {data &&
        data.map((element, index) => {
          return (
            <div className='box' key={element.id}>
              <img src={element.img} alt='img-product' className='img' />
              <h3>{element.nama}</h3>
              <h2>{element.harga}</h2>
              <span className='rate'>
                <img src={start} alt='img-rate' />
                <p>{element.rate}</p>
              </span>
            </div>
          );
        })}
    </div>
  );
};
export default List;
